/* =============== Advertisement =============== */

.offer-ad {
  background-color: #f8f8f8;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px 0;
  animation: fadeInUp 1s ease; 
  text-align: center;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* =========== Media Querys =============== */

@media (max-width: 600px) {
  .form-group {
    width: 100%; /* Adjusted width for smaller devices */
    height: auto; /* Adjusted height for smaller devices */
    margin-bottom: 0;
  }
  .form-group mt-2 {
    width: 100%; /* Adjusted width for smaller devices */
    height: auto; /* Adjusted height for smaller devices */
  }
}


/* Media queries for smaller screen sizes */

@media (max-width: 500px) {
  .title {
      font-size: 50px; /* Adjust font size for smaller screens */
  }

  

  .container-fluid {
      padding-left: 0;
      padding-right: 0;
      
  }

  .col-lg-7 {
      width: 100%; /* Make the video column take full width on smaller screens */
      padding-right: 0;
      
  }

  .col-lg-5 {
      width: 100%; /* Make the table column take full width on smaller screens */
      padding-left: 0;
  }

  .col-md-6 {
      width: 100%; /* Make both columns take full width on smaller screens */
      padding-left: 0;
      padding-right: 0;
  }

  .bottom-text {
      text-align: center; /* Center align text on smaller screens */
  }
}



